body {
  background-image: linear-gradient(to bottom, rgba(173,216,230,0), rgba(173,216,230,1));
  background-repeat: no-repeat;
  background-attachment: fixed;
  margin: 0;
}

.message {
  color: darkblue;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

.label {
  display: flex;
  justify-content: flex-start;
}

.buttons {
  display: flex;
  justify-content: space-between;
}

table.layout {
  width: 100%;
}

td.layout {
  display: flex;
  justify-content: center;
}

table.iteration {
  text-align: right;
  border-collapse: separate;
  border-spacing: 0px;
}

table.iteration th, td {
  padding-top: 4px;
  padding-left: 4px;
  padding-right: 4px;
}

table.iteration thead th {
  text-align: right;
  position: -webkit-sticky; /* Safari */
  position: sticky;
  top: 0;
  background-color: lightgrey;
  border-top: 2px solid darkblue;
  border-bottom: 2px solid darkblue;
  border-right: 2px solid darkblue;
}

table.iteration thead th:first-child {
  border-left: 2px solid darkblue;
}

table.iteration tbody tr:nth-child(even) {
  background-color: rgb(233, 233, 233);
}